@import url(https://fonts.googleapis.com/css?family=Supermercado+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Bigshot+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Bigelow+Rules|Bigshot+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

img {
  --webkit-box-shadow: -31px 10px 18px -8px rgba(0, 0, 0, 0.75);
  box-shadow: -31px 10px 18px -8px rgba(0, 0, 0, 0.75);
}

.root {
  display: flex;
  flex-flow: row wrap;
}

h1 {
  font-family: "Supermercado One";
  font-size: 2em;
  width: 50%;
  text-shadow: 1px 0px 1px #CCCCCC, 0px 1px 1px #EEEEEE, 2px 1px 1px
     #CCCCCC, 1px 2px 1px #EEEEEE, 3px 2px 1px #CCCCCC, 2px 3px 1px #EEEEEE,
     4px 3px 1px #CCCCCC, 3px 4px 1px #EEEEEE, 5px 4px 1px #CCCCCC, 4px 5px 1px
     #EEEEEE, 6px 5px 1px #CCCCCC, 5px 6px 1px #EEEEEE, 7px 6px 1px #CCCCCC;;

}

p {
  font-family: "Supermercado One";
}

.personaje {
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  width: 33% img;
  width-width: 50pt;
  width-height: auto;
}
.personaje li {
  list-style: square inside;
}
.personaje p {
  width: 33%;
}

.cabecera {
  height: 10em;
  background:url(../../static/media/marvel-twitter-header.560ad6e2.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: left;
  margin-bottom: 1em;
}
.cabecera h1 {
  color: white;
  font-family: "Bigshot One", cursive;
  font-family: "Bigelow Rules", cursive;
  margin-top: 0.5em;
  margin-left: 2em;
  font-size: 50pt;
}
@media (max-width: 800px) {
  .cabecera h1 {
    margin-top: 1em;
    margin-left: 2em;
    font-size: 30pt;
  }
 @media(max-width: 400px){
  .cabecera h1 {
    margin-top: 1em;
    margin-left: 2em;
    font-size: 20pt;
  }

 } 
}

.btncb {
  cursor: pointer;
  display: inline-block;
  text-decoration: none;
  text-align: center;
  margin-right: 1em;
  margin-left: 1em;
  margin-top: 2em;
  color: #fff;
  font-weight: bold;
  width: 5%;
  height: 15%;
  background-color: #538fbe;
  padding: 30px 20px;
  font-size: 1em;
  border: 1px solid #2d6898;
  background-image: -webkit-gradient(linear, left bottom, left top, from(#4984b4), to(#619bcb));
  background-image: linear-gradient(bottom, #4984b4 0%, #619bcb 100%);
  background-image: -webkit-linear-gradient(bottom, #4984b4 0%, #619bcb 100%);
  background-image: -ms-linear-gradient(bottom, #4984b4 0%, #619bcb 100%);
  background-image: -webkit-gradient(linear, left bottom, left top, color-stop(0, #4984b4), color-stop(1, #619bcb));
  border-radius: 5px;
  text-shadow: 0px -1px 0px rgba(0, 0, 0, 0.5);
  box-shadow: 0px 6px 0px #2b638f, 0px 3px 15px rgba(0, 0, 0, 0.4), inset 0px 1px 0px rgba(255, 255, 255, 0.3), inset 0px 0px 3px rgba(255, 255, 255, 0.5);
  -webkit-transition: all ease-in-out 0.5s;
  transition: all ease-in-out 0.5s;
  text-align: center;
}
@media (max-width: 850px) {
  .btncb {
    font-size: 0.6em;
  }
}
@media(max-width: 400px){
  .btncb{
    height: 2em;
    align-content: center;
  }
}

.btncb:hover {
  background-color: white;
  color: dimgrey;
  -webkit-transition: all ease-in-out 0.5s;
  transition: all ease-in-out 0.5s;
}
.container {
  display: flex;
  flex-flow: column wrap;
}
.infinite {
  height:700px;
  overflow:auto;

}
.comicContainer {
  display: flex;
  flex-flow: column wrap;
  align-content: center;}

.comicDetail { 
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
}  

